/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: "Inconsolata", monosapce;
  color: #0d1e2d;
}

.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 7px;
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

a {
  color: #777;
}

a:hover {
  color: #000;
}

h1, h2, h3, h5, h6, .font-heading {
  font-family: "Raleway", sans-serif;
  text-align: center;
}

header{
  font-family: "Raleway", sans-serif;
  text-align: left;
  margin-left: 100px;
  margin-top: 25px;
  margin-bottom: 0px;
  font-size: 30px;
}

.Row_cell {
  display: table;
  width: 80%; /*Optional*/
  table-layout: fixed; /*Optional*/
  border-spacing: 10px; /*Optional*/
  margin-left: 10%;
  margin-right: 40%;
  background-color: rgb(250,250,250);
  border-bottom-style: solid;
  border-bottom-width: 2px;
}

.Column_left {
  display: table-cell;
  width: 10% !important; /*Optional*/
}

.Column_right {
  position: absolute;
  margin-top: 5px;
  display: table-cell; /*Optional*/
  width: 90%;
}

.Column_left .Column_left-wrap img {
  transition: .3s transform ease;
  transform: scale(1);
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: block;
  height: 100%;
  width: 100%;

}

.button{
  width: 100%;
  font-size: 40px;
  border-width: 0;
  height: 200px;
}

.button:hover{background-color:white;}

.button:focus{
  background-color: white;
  border-color: white;
  outline-color: white;
}


.Row {
  display: table;
  width: 100%; /*Optional*/
  table-layout: fixed; /*Optional*/
  border-spacing: 10px; /*Optional*/
}
.Column_center {
  display: table-cell;
  width: 80%; /*Optional*/
}

.Column_side {
  position: absolute;
  margin-top: 20px;
  display: table-cell; /*Optional*/
  width: 10%;
  height: auto;
}

@media (max-width: 780px) {
  .custom-navbar > .container {
    padding-right: 0;
    padding-left: 0;
  }
}
.col-md-8 {
  flex: 0 0 45%;
  max-width: 50%;
}

.col-md-3 {
  flex: 0 0 35%;
  max-width: 40%;
}

.col-sm-5 {
  flex: 0 0 70%;
  max-width: 70%;
}


.section {
  padding: 1rem;
}

.filters a {
  color: #000;
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
}

.filters a:hover, .filters a:focus, .filters a:active {
  text-decoration: none;
}

.filters a:hover:before {
  content: "";
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 0;
  height: 1px;
  background-color: #000;
}

.filters a.active {
  color: #000;
}

.filters a.active:before {
  content: "";
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 0;
  height: 1px;
  background-color: #000;
}

.item {
  border: none;
  margin-bottom: 15px;
  padding-right: 5px;
  padding-left: 5px;

}

.item .item-wrap {
  display: block;
  position: relative;
  overflow: hidden;
}

.item .item-wrap:after {
  z-index: 2;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  visibility: visible;
  opacity: 0.75;
  transition: .3s all ease-in-out;
}

.item .item-wrap img {
  transition: .3s transform ease;
  transform: scale(1);
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: block;
  height: 100%;
  width: 100%;

}

.item .item-wrap > .event-info {
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
  z-index: 3;
  transform: translateY(-50%);
  color: #fff;
  opacity: 1;
  visibility: visible;
  margin-top: px;
  transition: .3s all ease;
}

.item .item-wrap > .event-info h3 {
  font-size: 20px;
  margin-bottom: 0;
}

.item .item-wrap > .event-info span {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: .2rem;
}

.item .item-wrap:hover {
  text-decoration: none;
}

.item .item-wrap:hover img {
  transform: scale(1.05);
}

.item .item-wrap:hover:after {
  opacity: 0;
  visibility: visible;
}

.item .item-wrap:hover .event-info {
  margin-top: 0px;
  opacity: 0;
  visibility: visible;
}

.ml-auto, .mx-auto {
  margin-left: unset !important;
}

.span{
  text-align: center;
}

.h3 {
  font-size: 20px;
  margin-top: 1rem;
}

.h4 {
  font-size: 16px;
  font-family: "Raleway", sans-serif;
  text-align: left;
  margin-left: 30px!important;
  margin-top: auto !important;
  margin-bottom: auto!important;
  
}


.mb-5 {
  margin-top: 1.5rem!important;
  margin-bottom: 1.5rem!important;
}

.i{
  margin-top: auto;
  margin-bottom: auto;
}

p {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.heading {
  font-size: 28px;
}
